	// JS 浮点数四则运算精度丢失解决方案
	// 加法
	Number.prototype.add = function (arg) {
	  var m, m1, m2;
	
	  try {
	    m1 = this.toString().split('.')[1].length;
	  } catch (e) {
	    m1 = 0;
	  }
	
	  try {
	    m2 = arg.toString().split('.')[1].length;
	  } catch (e) {
	    m2 = 0;
	  }
	
	  m = Math.pow(10, Math.max(m1, m2));
	  return (this.mul(m) + arg.mul(m)) / m;
	};
	
	// 减法
	Number.prototype.sub = function (arg) {
	  return this.add(-arg);
	};
	
	// 乘法
	Number.prototype.mul = function (arg) {
	  var m = 0;
	  var s1 = this.toString();
	  var s2 = arg.toString();
	
	  try {
	    m += s1.split('.')[1].length;
	  } catch (e) {}
	
	  try {
	    m += s2.split('.')[1].length;
	  } catch (e) {}
	
	  return Number(s1.replace('.', '')) * Number(s2.replace('.', '')) / Math.pow(10, m);
	};
	
	// 除法
	Number.prototype.div = function (arg) {
	  var m1 = 0;
	  var m2 = 0;
	  var n1, n2;
	
	  try {
	    m1 = this.toString().split('.')[1].length;
	  } catch (e) {}
	
	  try {
	    m2 = arg.toString().split('.')[1].length;
	  } catch (e) {}
	
	  n1 = Number(this.toString().replace('.', ''));
	  n2 = Number(arg.toString().replace('.', ''));
	  // return (n1 / n2) * Math.pow(10, m2 - m1);
	  return (n1 / n2).mul(Math.pow(10, m2 - m1));
	};
	
	var B = {};
	B.url = {};
	B.url.getParam = function (name, url) {
	  url = url || location.href;
	  var reg = new RegExp("(^|&|\\?|#)" + name + "=([^&]*?)(&|#|$)");
	  var tempHash = url.match(/#.*/) ? url.match(/#.*/)[0] : "";
	  url = url.replace(/#.*/, "");
	
	  if (reg.test(tempHash)) {
	    return decodeURIComponent(tempHash.match(reg)[2]);
	  } else if (reg.test(url)) {
	    return decodeURIComponent(url.match(reg)[2]);
	  } else {
	    return '';
	  }
	};
	
	// 时间格式化函数 author: meizz
	// (new Date()).format("yyyy-MM-dd hh:mm:ss.S") => 2006-07-02 08:09:04.423
	// (new Date()).format("yyyy-M-d h:m:s.S")      => 2006-7-2 8:9:4.18
	Date.prototype.format = function (fmt) {
	  var o = {
	    "M+": this.getMonth() + 1, //月份
	    "d+": this.getDate(), //日
	    "h+": this.getHours(), //小时
	    "m+": this.getMinutes(), //分
	    "s+": this.getSeconds(), //秒
	    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
	    "S": this.getMilliseconds() //毫秒
	  };
	  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
	  for (var k in o) if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
	  return fmt;
	};
	
	B.url.setParam = function (name, value, url, isHashMode) {
	  if (typeof name === 'undefined' || typeof value === 'undefined' || typeof url === 'undefined') {
	    return url;
	  }
	  var separator,
	      reg = new RegExp("(^|&|\\?|#)" + name + "=([^&]*?)(&|#|$)"),
	      tempHash = url.match(/#.*/) ? url.match(/#.*/)[0] : "";
	
	  url = url.replace(/#.*/, '');
	  if (isHashMode === true) {
	    if (reg.test(tempHash)) {
	      tempHash = tempHash.replace(reg, function (m, r1, r2, r3) {
	        return r1 + name + "=" + encodeURIComponent(value) + r3;
	      });
	    } else {
	      separator = tempHash.indexOf("#") === -1 ? "#" : "&";
	      tempHash = tempHash + separator + name + "=" + encodeURIComponent(value);
	    }
	    tempHash = tempHash.replace(reg, function (m, r1, r2, r3) {
	      return r1 + name + "=" + encodeURIComponent(value) + r3;
	    });
	    return tempHash + url;
	  } else if (reg.test(url)) {
	    url = url.replace(reg, function (m, r1, r2, r3) {
	      return r1 + name + "=" + encodeURIComponent(value) + r3;
	    });
	  } else {
	    separator = url.indexOf("?") === -1 ? "?" : "&";
	    url = url + separator + name + "=" + encodeURIComponent(value);
	  }
	  return url + tempHash;
	};
	
	B.url.parseHash = function (hash) {
	  var tag,
	      query,
	      param = {};
	  var arr = hash.split('?');
	  tag = arr[0];
	
	  if (arr.length > 1) {
	    var seg, s;
	    query = arr[1];
	    seg = query.split('&');
	
	    for (var i = 0; i < seg.length; i++) {
	      if (!seg[i]) continue;
	      s = seg[i].split('=');
	      param[s[0]] = s[1];
	    }
	  }
	
	  return {
	    hash: hash,
	    tag: tag,
	    query: query,
	    param: param
	  };
	};
	
	B.url.serializeArray = function (params) {
	  var param = {},
	      temp;
	
	  try {
	    params = params.split('&');
	    for (var i = 0, len = params.length; i < len; i++) {
	      if (!params[i]) continue;
	      temp = params[i].split('=');
	      param[temp[0]] = temp[1];
	    }
	  } catch (err) {
	    alert('解析公用参数出错：' + err);
	  }
	
	  return param;
	};
	
	// 将 url 中的参数全部提取到一个对象中并返回
	B.url.getObj = function (url) {
	  var obj = B.url.parseHash(url);
	  return obj.param;
	};
	
	B.url.setObj = function (url, obj) {
	  $.each(obj, function (key, value) {
	    url = B.url.setParam(key, value, url);
	  });
	  return url;
	};
	
