ddb.isApp = /DaDaBusPassenger/i.test(navigator.userAgent);
// ddb.android = ua.match(/(Android);?[\s\/]+([\d.]+)?/);
ddb.isAndroidApp = ddb.isApp && $.os.android;
ddb.isIosApp = ddb.isApp && $.os.ios;
// ddb.env = 'release'; // 正式环境
// ddb.env = 'pre'; // 预发布环境
// ddb.env = 'test'; // 测试环境
// ddb.env = 'dev'; // 本地调试
