// 根据 city code 获取城市名称-依赖城市列表数据
ddb.getCityByCode = function(code, citys) {
  var city = '';
  citys = citys || ddb.CITYS;

  if (!citys) {
    return city;
  }

  citys.some(function(item) {
    if (item.code == code) {
      city = item.city;
      return true; // 找到后退出循环
    }
  });

  return city;
};

