// ios APP 方法准备完成函数
ddb.appready = (function() {
  var doc = window.document;
  var loaded = !!window.DDBApp;
  var fns = [], fn;

  var id = setInterval(function() {
    if (window.DDBApp) {
      clearInterval(id);
      flush();
    }
  }, 50);

  doc.addEventListener('DDBAppReady', fn = function() {
    doc.removeEventListener('DDBAppReady', fn);
    flush();
  });

  function flush(f) {
    loaded = 1;
    while (f = fns.shift()) {
      f();
    }
  }

  return function(fn) {
    if (ddb.isApp) {
      loaded ? fn() : fns.push(fn);
    }
  };
}());

